import React, {Component} from 'react';
import { NavLink } from 'react-router-dom';
import {Input, Select, Button, Radio, Table, Switch, DatePicker, Icon} from 'antd';

const Option = Select.Option;

class AdvertiserList extends Component {
	state = {
		columns: [{
			title: 'ID',
			dataIndex: 'ID',
			key: 'ID',
			render: text => <a href="javascript:;">{text}</a>,
		}, {
			title: '名称',
			dataIndex: 'name',
			key: 'name',
		}, {
			title: '广告主',
			dataIndex: 'user',
			key: 'user',
		}, {
			title: '消耗',
			key: 'resume',
			dataIndex: 'resume'
		},{
			title: '状态',
			key: 'state',
			dataIndex: 'state',
			render:()=>{
				return (
					<div>
						<Switch checkedChildren="启用" unCheckedChildren="暂停" style={{ marginRight:'10px',verticalAlign:'middle' }} />
						{/*<div style={{ display:'inline-block',verticalAlign:'middle' }}>*/}
							{/*<span className="dot"></span>*/}
							{/*<span>审核成功</span>*/}
						{/*</div>*/}
						<Button style={{ float:'right' }}><NavLink to="/ad/project/detail">详情</NavLink></Button>
					</div>
				)
			}
		}],
		data:[{
			key: '1',
			ID: '1208684444',
			name: '网赚_wym_0412_2',
			user:'浙江宅神网络科技有限公司',
			resume: '￥0.00'
		}]
	};

	render() {
		return (
			<div className="projectList">
				<div className="search">
					<Input placeholder="查询广告项目" style={{flex: 10, marginRight: '6px'}}/>
					<Select placeholder="选择状态" style={{width: '200px', marginRight: '6px'}}>
						<Option value="online">上线</Option>
						<Option value="stop">暂停</Option>
					</Select>
					<DatePicker placeholder="开始日期" style={{width: '200px', marginRight: '6px'}} />
					<DatePicker placeholder="结束日期" style={{width: '200px', marginRight: '6px'}} />
					<Button style={{width: '65px'}} type="primary">查询</Button>
				</div>
				<div className="grid">
					<div style={{textAlign: 'right'}}>
						<Radio.Group value='unRegister'>
							<Radio.Button value="unRegister">未归档</Radio.Button>
							<Radio.Button value="register">已归档</Radio.Button>
						</Radio.Group>
					</div>
					<Button type="primary" style={{ position:'absolute',right:45,top:70,zIndex:99 }}><Icon type="plus"></Icon>新建</Button>
					<Table columns={this.state.columns} dataSource={this.state.data} />
				</div>
			</div>
		)
	}
}

export default AdvertiserList;