import React, {Component} from 'react';
import {Input,Button,Icon,Alert} from 'antd';

class Login extends Component {
	constructor(props) {
		super(props);
		this.state = {
			userName:'',
			pwd:'',
			login:true,
			errorMsg:''
		}
		this.userChange = this.userChange.bind(this);
		this.pwdChange = this.pwdChange.bind(this);
		this.login = this.login.bind(this);
	}
	userChange (e) {
		console.log(e.currentTarget.value);
		this.setState({
			userName:e.currentTarget.value
		})
	}
	pwdChange (e) {
		this.setState({
			pwd:e.currentTarget.value
		})
	}
	login () {
		if (!this.state.userName || !this.state.pwd) {
			this.setState({
				errorMsg:'账号或密码不能为空',
				login:false
			})
		}else if (this.state.userName === 'admin' && this.state.pwd === '123456') {
			this.props.history.push('/advertiser/list');
		} else {
			this.setState({
				errorMsg:'账号或密码错误！',
				login:false
			})
		}
		setTimeout(()=>{
			this.setState({
				login:true
			})
		},2000)
	}
	render() {
		return (
			<div className="login">
				<div className="logo-part">
					<div className="logo"></div>
					<div className="logoTitle">AdxHi广告管理系统</div>
				</div>
				<div className="login-form">
					<div className="formTitle">登录</div>
					<div className="login-field">
						<Input placeholder="请输入用户名" prefix={<Icon type="user" style={{ color: '#fff' }} />} style={{height:'100%'}} onChange={this.userChange}/>
					</div>
					<div className="login-field">
						<Input placeholder="请输入密码" type="password" prefix={<Icon type="lock" style={{ color: '#fff' }} />} style={{height:'100%'}} onChange={this.pwdChange} />
					</div>
					<Button className="login-submit" onClick={this.login}>登录</Button>
				</div>
				<div className={this.state.login ? 'alertBox right':'alertBox error'}>
					<Alert
						message={this.state.errorMsg}
						type="error"
						showIcon
					/>
				</div>
			</div>
		)
	}
}

export default Login;